import "./style.css";
import "./script";
import $ from "jquery";
import Bib from "./bib.jpg";
import { Link } from "react-router-dom";
import {
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBCard,
  MDBInput,
  MDBBtn
} from "mdbreact";
import {
  Carousel,
  CarouselItem,
  CarouselControl,
  CarouselIndicators,
  CarouselCaption,
  Button
} from "reactstrap";

const items = [
  {
    src:
      "https://www.museeyslmarrakech.com/wp-content/uploads/2017/10/BIBLIOTHEQUE.jpg",
    altText: "Slide 1",
    caption: "La biblioth√©que"
  },
  {
    src:
      "https://www.archimag.com/sites/archimag.com/files/styles/article/public/web_articles/image/bibpublique_stokholm.jpg?itok=E47gGaDp",
    altText: "le calme",
    caption: "le calme"
  },
  {
    src:
      "https://www.ensta-paristech.fr/sites/default/files/images/Bibliotheque/biblio1.jpg",
    altText: "Slide 3",
    caption: "le silence "
  },
  {
    src:
      "https://www.sciencesetavenir.fr/assets/img/2016/05/16/cover-r4x3w1000-57dfa8e274927-salle-de-cinema.jpg",
    altText: "Slide 3",
    caption: "regarder un film "
  }
];

class Example extends React.Component {
  componentDidMount() {
    const h1 = $("h1");
    console.log(h1);
    const h = $("img");
    console.log(h);
    const h2 = $("img1");
    console.log(h2);
    const h3 = $("img2");
    console.log(h3);
    const h4 = $("jamoussi");
    console.log(h4);
  }
  constructor(props) {
    super(props);
    this.state = { activeIndex: 0 };
    this.next = this.next.bind(this);
    this.previous = this.previous.bind(this);
    this.goToIndex = this.goToIndex.bind(this);
    this.onExiting = this.onExiting.bind(this);
    this.onExited = this.onExited.bind(this);
    this.Click = this.Click.bind(this);
  }
  Click() {
    document.getElementById("id01").style.display = "none";
  }
  onExiting() {
    this.animating = true;
  }

  onExited() {
    this.animating = false;
  }

  next() {
    if (this.animating) return;
    const nextIndex =
      this.state.activeIndex === items.length - 1
        ? 0
        : this.state.activeIndex + 1;
    this.setState({ activeIndex: nextIndex });
  }

  previous() {
    if (this.animating) return;
    const nextIndex =
      this.state.activeIndex === 0
        ? items.length - 1
        : this.state.activeIndex - 1;
    this.setState({ activeIndex: nextIndex });
  }

  goToIndex(newIndex) {
    if (this.animating) return;
    this.setState({ activeIndex: newIndex });
  }

  render() {
    const { activeIndex } = this.state;

    const slides = items.map(item => {
      return (
        <CarouselItem
          onExiting={this.onExiting}
          onExited={this.onExited}
          key={item.src}
        >
          <img
            className="col-sm-12"
            height={470}
            width={1110}
            src={item.src}
            alt={item.altText}
          />
          <CarouselCaption
            captionText={item.caption}
            captionHeader={item.caption}
          />
        </CarouselItem>
      );
    });

    return (
      <div id="div1">
        <div className="container">
          <div className="row">
            <h1 className="col-sm-6 text-center " id={"h1"}>
              Bienvenue chez El Jamoussi
              <img
                src="data:image/jpeg;base64,"
                width="200px"
                alt="jamoussi"
                id={"jamoussi"}
                className="col-sm-4"
              />
            </h1>
            <span className="col-sm-6">
              <a href="#sing">
                <Button color="warning">Se Connecter</Button>
              </a>
              <a href="#sing">
                <Button color="info">Inscription</Button>
              </a>
            </span>

            <Carousel
              className="col-sm-12"
              activeIndex={activeIndex}
              next={this.next}
              previous={this.previous}
            >
              <CarouselIndicators
                items={items}
                activeIndex={activeIndex}
                onClickHandler={this.goToIndex}
              />
              {slides}
              <CarouselControl
                direction="prev"
                directionText="Previous"
                onClickHandler={this.previous}
              />
              <CarouselControl
                direction="next"
                directionText="Next"
                onClickHandler={this.next}
              />
            </Carousel>

            <div className="col-sm-12 text-uppercase">
              <center>
                <h2 id="services">Nos Services</h2>
              </center>
            </div>

            <div className="col-sm-6 image ">
              <img
                id={"img1"}
                src={Bib}
                width={250}
                className="rounded-circle"
                alt="biblioth√©que"
              />
              <br />
              <p className={"  text-justify text-center "}>
                Vous pouvez acc√©der aux services de la biblioth√©que
              </p>
            </div>
            <div className="col-sm-6 image">
              <img
                id={"img"}
                src="https://media-cdn.tripadvisor.com/media/photo-s/0e/69/76/c2/sanno-cafeteria.jpg"
                width={250}
                alt="caf√©"
                className="rounded-circle"
              />
              <p className={" text-justify text-center "}>
                Vous pouvez acc√©der aux services <br />
                de la caf√©teria
              </p>
            </div>

            <div className=" col align-self-center image ">
              <img
                id={"img2"}
                src="https://media-cdn.tripadvisor.com/media/photo-s/07/d4/71/0c/level-up.jpg"
                width={250}
                alt="jeux"
                className="rounded-circle"
              />
              <p className={"text-justify text-center"}>
                Vous pouvez acc√©der √†
                <br /> la salle des jeux
              </p>
            </div>
            <div className=" col align-self-end image ">
              <img
                id={"img3"}
                src="https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSN_tZja9EVuAhf1ddBwII2Tfks1pXTu2QLgMs7j-RhsbAAwFxlGQ"
                width={250}
                alt="film"
                className="rounded-circle"
              />
              <p className={"text-justify text-center"}>
                Vous pouvez acc√©der √†
                <br /> la salle de cin√©ma
              </p>
            </div>
            <div className="col-sm-12 text-center text-blod">
              <p id="paraghraphe">
                Si Vous √©tes tr√©s int√©r√©ss√©s par nos services vous pouvez nous
                rejoindre
              </p>
            </div>

            <div className="col-sm-12">
              <MDBContainer>
                <MDBRow>
                  <MDBCol calssName="col-sm-12">
                    <MDBCard
                      className="card-image "
                      style={{
                        backgroundImage:
                          "url(https://www.tunisie.co/uploads/images/content/jamousi-120517-v.jpg)",
                        backgroundAttachmebt: "fixed",
                        backgroundPosition: "center",
                        backgroundSize: "cover"
                      }}
                    >
                      <div className="text-white rgba-stylish-strong py-5 px-5 z-depth-4">
                        <div className="text-center">
                          <h3 className="white-text mb-5 mt-4 font-weight-bold">
                            <a
                              id="sing"
                              className="green-text font-weight-bold"
                            >
                              <strong> Inscription</strong>
                            </a>
                          </h3>
                        </div>
                        <MDBInput
                          label="votre email"
                          group
                          type="text"
                          validate
                          labelClass="white-text"
                          placeholder="email"
                        />
                        <MDBInput
                          label="votre mot de passe"
                          group
                          type="password"
                          validate
                          labelClass="white-text"
                          palceholder="mot de passe"
                        />
                        <div className="md-form pb-3">
                          <MDBInput
                            label={
                              <>
                                Accepter les&nbsp;
                                <a
                                  href="#!"
                                  className="green-text font-weight-bold"
                                >
                                  Termes et les Conditions
                                </a>
                              </>
                            }
                            type="checkbox"
                            id="checkbox1"
                            labelClass="white-text"
                          />
                        </div>
                        <MDBRow className="d-flex align-items-center mb-4">
                          <div className="text-center mb-3 col-sm-12">
                            <Link to="/Login">
                              {" "}
                              <MDBBtn
                                color="success"
                                rounded
                                type="button"
                                className="btn-block z-depth-1"
                              >
                                Se connecter
                              </MDBBtn>
                            </Link>
                          </div>
                        </MDBRow>
                        <MDBCol sm="6">
                          <p className="  font-small white-text d-flex justify-content-center">
                            vous avez un compte?
                            <a
                              href="#!"
                              className="green-text ml-1 font-weight-bold"
                            >
                              se connecter
                            </a>
                          </p>
                        </MDBCol>
                      </div>
                    </MDBCard>
                  </MDBCol>
                </MDBRow>
              </MDBContainer>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Example;
